import { floatTool } from '@/utils/chartUtil'
function transform(prop, value, series, index) {
    let obj = {
        attr: null,
        result: value,
        value,
        series
    }

    // 如果没有formatter
    if (!series.format) {
        series.format = {
            format: "{b}",
            ratio: "1",
            digit: "auto",
            prefix: "",
            suffix: ""
        }
    }

    let final = {}
    let action = new Map([
        ['pieType', () => getAttr(obj, 'roseType', final)],
        ['radius', () => {
            obj.result = obj.result.split('-')
            getAttr(obj, 'radius', final)
        }],
        ['cusradius', () => {
            obj.result = [obj.result[0] + '%', obj.result[1] + '%']
            getAttr(obj, 'radius', final)
        }],
        ['center', () => {
            obj.result = obj.result.split('-')
            getAttr(obj, 'center', final)
        }],
        ['cuscenter', () => {
            obj.result = [obj.result[0] + '%', obj.result[1] + '%']
            getAttr(obj, 'center', final)
        }],
        ['clockwise', () => getAttr(obj, 'clockwise', final)],
        ['startAngle', () => getAttr(obj, 'startAngle', final)],
        ['cusAngle', () => getAttr(obj, 'startAngle', final)],
        ['label.show', () => getFullAttr(obj, 'label.show', final)],
        ['label.position', () => getFullAttr(obj, 'label.position', final)],
        ['label.fontSize', () => getFullAttr(obj, 'label.fontSize', final)],
        ['labelLine.show', () => getFullAttr(obj, 'labelLine.show', final)],
        ['labelLine.smooth', () => getFullAttr(obj, 'labelLine.smooth', final)],
        ['labelLine.lineStyle.type', () => getFullAttr(obj, 'labelLine.lineStyle.type', final)],
        ['labelLine.lineStyle.width', () => getFullAttr(obj, 'labelLine.lineStyle.width', final)],
        ['labelLine.length', () => getFullAttr(obj, 'labelLine.length', final)],
        ['labelLine.length2', () => getFullAttr(obj, 'labelLine.length2', final)],
        ['series.show', () => {
            
        }],

        ['textPos', () => getStyle(obj, 'label', 'position', final)],
        ['fontPlace', () => {
            if (value.includes('bold')) {
                getStyle(obj, 'label', 'fontWeight', final, 'bold')
            } else {
                getStyle(obj, 'label', 'fontWeight', final, 'normal')
            }
            if (value.includes('italic')) {
                getStyle(obj, 'label', 'fontStyle', final, 'italic')
            } else {
                getStyle(obj, 'label', 'fontStyle', final, 'italic')
            }
        }],
        ['fontSize', () => getStyle(obj, 'label', 'fontSize', final)],
        ['offsetX', () => {
            let data = [value + '%', series.label.offset ? series.label.offset[1] : 0]
            getStyle(obj, 'label', 'offset', final, data)
        }],
        ['offsetY', () => {
            let data = [series.label.offset ? series.label.offset[0] : 0, value + '%']
            getStyle(obj, 'label', 'offset', final, data)
        }],
        ['customSize', () => getStyle(obj, 'label', 'fontSize', final)],
        ['showStack', () => {
            if (value) {
                final.stack = value
            } else {
                final.stack = null
            }
        }],
        ['stackValue', () => getAttr(obj, 'stack', final)],
        ['barCategoryGap', () => getAttr(obj, 'barCategoryGap', final)],
        ['cusbarCategoryGap', () => getAttr(obj, 'barCategoryGap', final)],
        ['format-format', () => series.format['format'] = value],
        ['format-digit', () => series.format['digit'] = value],
        ['format-ratio', () => series.format['ratio'] = value],
        ['format-prefix', () => series.format['prefix'] = value],
        ['format-suffix', () => series.format['suffix'] = value]
    ])

    action.get(prop)()

    // 调整formatter形式
    if (prop.includes('format')) {
        let formatter = formatData(series.format)
        getStyle(obj, 'label', 'formatter', final, formatter)
        getAttr(obj, 'format', final, series.format)
    }

    return final
}

// 属性赋值
function getAttr(obj, attr, final, value) {
    obj.attr = attr
    if(value){
        final[obj.attr] = value
    }else{
        final[obj.attr] = obj.result
    }
}
// 对象属性赋值
function getStyle(obj, desc, field, final, value) {
    obj.attr = desc
    if(!obj.series[obj.attr]){
        obj.series[obj.attr] = {}
    }
    if (value) {
        obj.series[obj.attr][field] = value
    } else {
        obj.series[obj.attr][field] = obj.value
    }
    obj.result = obj.series[obj.attr]

    getAttr(obj, desc, final)
}

// 
function getFullAttr(obj, attr, final, value){
    let arr = attr.split('.')
    function repeat(obj, final, value){
        for(let i = 0; i < arr.length; i++){
            if(i != arr.length - 1){
                let field = arr[0]
                final[field] = {}
                arr.shift()
                repeat(obj, final[field], value)
            }else{
                if(value){
                    final[arr[0]] = value
                }else{
                    final[arr[0]] = obj[attr]
                }
            }
        }
    }
    repeat(obj, final, value)
}

// 
function getFullSeriesAttr(obj, attr, final, value, index){
    final.data = obj.series.data
    let arr = attr.split('.')
    arr.shift()
    function repeat(obj, final, value){
        for(let i = 0; i < arr.length; i++){
            if(i != arr.length - 1){
                let field = arr[0]
                final.data[index][field] = {}
                arr.shift()
                repeat(obj, final.data[index][field], value)
            }else{
                if(value){
                    final[arr[0]] = value
                }else{
                    final[arr[0]] = obj[attr]
                }
            }
        }
    }
    repeat(obj, final, value)
}

// formatter转换
function formatData(format) {
    let fun
    if (format.format == '{c}') {
        fun = function (params) {
            if (format.digit == 'default') {
                return (
                    format.prefix.trim() +
                    floatTool.multiply(params.value, format.ratio) +
                    format.suffix.trim()
                )

            } else {
                return (
                    format.prefix.trim() +
                    floatTool
                        .multiply(params.value, format.ratio)
                        .toFixed(format.digit) +
                    format.suffix.trim()
                )
            }
        }
    }

    return fun
}

const transformPie = function (chartAllTypeArray, pieSeries, series) {
    const chartPro = chartAllTypeArray[0];
    const chartType = chartAllTypeArray[1];
    const chartStyle = chartAllTypeArray[2];

    // 更新当前修改的属性
    let prop = pieSeries.prop 
    let index = pieSeries.currentIndex
    let value
    if(prop){
        prop = pieSeries.prop
        if(prop == 'cuscenter'){
            value = [pieSeries['cuscenter1'], pieSeries['cuscenter2']]
        }else{
            value = pieSeries[prop]       
        }
        let result = transform(prop, value, series[0], pieSeries, index)
        Object.assign(series[0], result)
    }

    return series
}

export default transformPie